import {
    toast,
    goLoginWithWX,
    startLoginWithWX,
    goLoginWithPhone,
    bindWxWithCallback,
} from '@/utils/jsApiV2/client';
import {
    getLoginStatus,
} from '@/api/withdraw';

export default {
    props: {
        isVisitor: {
            type: Boolean,
            default: false,
        },
        isWechatAuth: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            showLoginDialog: false,
            wechatNickname: '',
            wechatCode: '',
            bindWechatSuccessCallback: null,
            wechatCodeCallback: 'wechatCodeCallback',
            wechatLoginCallback: 'wechatLoginCallback',
            wechatCodeLoginCallback: 'wechatCodeLoginCallback',
        };
    },
    created() {
        this.registerWechatCallback();
    },
    methods: {
        registerWechatCallback() {
            window[this.wechatCodeCallback] = this.getWechatCode;
            window[this.wechatCodeLoginCallback] = this.getWechatCodeAndLogin;
            window[this.wechatLoginCallback] = this.handleLoginResult;
        },
        getWechatCode({ code } = {}) {
            if (!code) {
                return toast('微信授权失败，请重试');
            }

            this.wechatCode = code;

            return getLoginStatus({
                type: this.type,
                key: code,
            })
                .then(({ hasBind, nickname }) => {
                    if (hasBind) {
                        this.wechatNickname = nickname;

                        // 存在老账号，清空提现回调
                        if (this.bindWechatSuccessCallback) {
                            this.bindWechatSuccessCallback = null;
                        }

                        this.openLoginDialog();
                    } else {
                        startLoginWithWX(this.wechatCodeLoginCallback);
                    }
                })
                .catch(() => this.$error());
        },
        getWechatCodeAndLogin({ code }) {
            this.wechatCode = code;

            goLoginWithWX(this.wechatCode, this.wechatLoginCallback);
        },
        handleLoginResult(isSuccess) {
            if (isSuccess) {
                if (this.showLoginDialog) this.closeLoginDialog();

                // 微信绑定成功，调用成功回调（发提现请求）
                if (this.bindWechatSuccessCallback) {
                    this.bindWechatSuccessCallback();
                    this.bindWechatSuccessCallback = null;
                }

                this.$emit('login');
            } else {
                toast('登录失败，请重试');
            }
        },
        handleBindWechat(callback) {
            this.bindWechatSuccessCallback = callback;

            if (this.isVisitor) {
                startLoginWithWX(this.wechatCodeCallback);
            } else {
                bindWxWithCallback(this.wechatLoginCallback);
            }
        },
        handleBindPhone() {
            goLoginWithPhone();
        },
        handleLogin() {
            this.$o2o({
                e_c: '提现',
                e_a: '点击',
                e_n: '切换账号弹窗点击继续登录',
            });

            startLoginWithWX(this.wechatCodeLoginCallback);
        },
        openLoginDialog() {
            this.$o2o({
                e_c: '提现',
                e_a: '曝光',
                e_n: '提现切换账号弹窗曝光',
            });

            this.showLoginDialog = true;
        },
        closeLoginDialog() {
            this.$o2o({
                e_c: '提现',
                e_a: '点击',
                e_n: '切换账号弹窗点击取消',
            });

            this.showLoginDialog = false;
        },
    },
};
