<template>
    <div class="withdraw">
        <nav-bar
            v-if="isShowNavbar()"
            title="提现兑换"
            :hairline="false">
            <span
                @click="convertDescribe">
                兑换说明
            </span>
        </nav-bar>
        <div class="withdraw__base">
            <balance
                label='我的分红'
                :cash="withdrawInfo.cash">
                <span
                    class="withdraw__progress"
                    @click="goProgress">提现进度</span></balance>
            <div class="withdraw__label">
                <div class="withdraw__label_txt">金币兑换：</div>
            </div>
            <withdraw-options
                :value="value"
                :cash="withdrawInfo.cash"
                :options="withdrawInfo.withdrawOptions"
                @input="selectCash" />
            <div class="withdraw__hint">
                <p>注意事项:</p>
                <p>1.提现申请将在10分钟内审批到账，如遇节假日时间顺延</p>
                <p>2.连续签到{{ limitSignDays }}天即可提现，两次提现间隔需大于3天。</p>
                <p>3.因微信提现每日有额度上限，若遇到提示“今日额度已发完”，请明日再来兑换</p>
                <p>4.如兑换遇到问题，请在「想看资讯」公众号里反馈</p>
                <p>5.最终解释权归想看App所有</p>
            </div>
            <div class="withdraw__btn">
                <Button
                    v-if="withdrawInfo.withdrawOptions && withdrawInfo.withdrawOptions.length"
                    :loading="loading"
                    :disabled="!value.coin"
                    @click="withdrawHandler(handleWithdrawSuccess, handleWithdrawError)">
                    立即兑换</Button>
            </div>
            <withdraw-dialog
                :msgs="reasons"
                :btn-text="reasonBtnText"
                :click-handler="reasonHandler"
                :show.sync="showReasonDialog" />
        </div>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import { getPartnerWithdrawInfo, postPartnerWithdraw } from '@/api/partner';
import { isXiangkan } from '@/utils/ua';
import Button from '@/components/Button';
import miVerify from '@/utils/miVerify';
import config from '@/config';
import { set } from '@/utils/cache';
import {
    addCalendarEvent,
    setSwipeBackStatus,
    openCommonWebViewByPath,
    toast,
    hasCalendarEventExist,
    quitCurrentWebview,
} from '@/utils/jsApiV2/client';
import {
    goTab,
} from '@/utils/jsApiV2/deeplink';
import debounce from 'lodash.debounce';
import Balance from './WithdrawComponents/Balance';
import WithdrawOptions from '../../withdraw/WithdrawOptions';
import WithdrawDialog from '../../withdraw/WithdrawDialog';
import withdrawVisitorMixin from '../../withdraw/withdrawVisitorMixin';
import '../../../../static/v';

const cacheSelectedCashKey = 'selectedCash';

export default {
    name: 'PartnerWithdraw',
    mixins: [withdrawVisitorMixin],
    data() {
        return {
            withdrawInfo: {},
            value: {},
            loading: false,
            showReasonDialog: false,
            reasons: [],
            reasonBtnText: '',
            reasonHandler: this.closeReasonDialog,
            defaultReasons: ['网络不太好，请稍后重试'],
            defaultReasonBtnText: '我知道了',
        };
    },
    created() {
        this.$root.hidePlaceholder();
        this.getPartnerWithdrawInfo();
        this.$o2o({
            e_c: '地推提现曝光',
            e_a: '曝光',
            e_n: '地推合伙人平台提现页',
        });
        // 人机验证初始化
        miVerify(
            { type: '时长任务' },
            (verify) => {
                window.miVerify.partnerWithdraw.verify = verify;
            },
            'partnerWithdraw',
        );
    },
    components: {
        NavBar,
        Balance,
        WithdrawOptions,
        WithdrawDialog,
        Button,
    },
    computed: {
        limitSignDays() {
            return (this.value && this.value.limitSignDays) || 3;
        },
    },
    methods: {
        isShowNavbar() {
            return isXiangkan();
        },
        convertDescribe() {
            openCommonWebViewByPath('/mobile-v2/withdraw/withdraw-manual');
            this.$o2o({ e_c: '提现', e_a: '点击', e_n: '地推合伙人平台提现页' });
        },
        getPartnerWithdrawInfo() {
            getPartnerWithdrawInfo().then((res) => {
                this.withdrawInfo = res;
                // 进入提现兑换页面，如果金币资产充足，则选中满足条件的最大金额档位
                // eslint-disable-next-line max-len
                this.value = this.getMaxCashOption(this.withdrawInfo.coin, this.withdrawInfo.withdrawOptions);
            }).catch((e) => {
                console.error(e);
                this.$error();
            });
        },
        goProgress() {
            this.$o2o({
                e_c: '提现',
                e_a: '点击',
                e_n: '地推合伙人平台提现进度按钮点击',
            });
            openCommonWebViewByPath('/mobile-v2/withdraw/progress');
        },
        selectCash(cash) {
            this.value = cash;
        },
        getMaxCashOption(coin, options) {
            let maxCashOption = {};
            let option;
            for (let i = 0; i < options.length; i += 1) {
                option = options[i];

                if (option.canClick
                    && coin >= option.coin
                    && (maxCashOption.coin || 3000) <= option.coin) {
                    if (option.coin === 3000) return option;

                    maxCashOption = option;
                }
            }
            return maxCashOption;
        },
        withdrawHandler: debounce(function handler(onSuccess, onError) {
            if (!this.value) return;
            this.$o2o({
                e_c: '立即兑换',
                e_a: '点击',
                e_n: '地推合伙人平台提现页',
            });
            this.withdraw({}, onSuccess, onError);
        }, 200),
        withdraw(params, onSuccess, onError) {
            const typeName = '想看合伙人提现';
            if (window.miVerify.register === false) {
                toast('网络异常，请退出后重试');
                return;
            }
            if (!window.miVerify.partnerWithdraw.verify) {
                toast('网络延迟，请稍后重试');
                return;
            }
            // 待添加验证步骤
            window.miVerify.partnerWithdraw.onSuccessRequest = (token) => {
                this.loading = true;
                setSwipeBackStatus('true');
                this.$o2o({
                    e_c: '滑动验证码',
                    e_a: '成功',
                    e_n: '滑动验证码解锁成功',
                    e_x: { trigger_source: `${typeName}提现` },
                });
                postPartnerWithdraw({
                    type: this.value.type || 'wechat',
                    cash: this.value.cash,
                    ...token,
                })
                    .then((res) => {
                        if (typeof onSuccess === 'function') {
                            onSuccess(res);
                        }
                        this.loading = false;
                    })
                    .catch((res) => {
                        if (res && res.status === '1001') {
                            this.$o2o({
                                e_c: '滑动验证码',
                                e_a: '失败',
                                e_n: '滑动验证码解锁失败',
                                e_x: { trigger_source: `${typeName}提现` },
                            });
                        }

                        this.$o2o({
                            e_c: '提现',
                            e_a: '失败',
                            e_n: '提现失败',
                            e_x: { type: typeName },
                        });

                        if (typeof onError === 'function') {
                            onError(res);
                        }

                        this.loading = false;
                    });
            };
            window.miVerify.partnerWithdraw.onCloseRequest = (code) => {
                setSwipeBackStatus('true');
                switch (code) {
                case 0:
                    this.$o2o({ e_c: '滑动验证码', e_a: '成功', e_n: '滑动验证码解锁成功', e_x: { trigger_source: `${typeName}提现` } }); break;
                case 1:
                    this.$o2o({ e_c: '滑动验证码', e_a: '关闭', e_n: '滑动验证码手动关闭', e_x: { trigger_source: `${typeName}提现` } }); break;
                case 2:
                    this.$o2o({ e_c: '滑动验证码', e_a: 'eventId 过期', e_n: '滑动验证码 eventId 过期', e_x: { trigger_source: `${typeName}提现` } }); break;
                case 3:
                    this.$o2o({ e_c: '滑动验证码', e_a: '系统错误', e_n: '滑动验证码系统错误', e_x: { trigger_source: `${typeName}提现` } }); break;
                default:
                    break;
                }
            };
            setSwipeBackStatus('false');
            window.miVerify.partnerWithdraw.verify.start({
                activeVerify: 'false',
            });
        },
        handleWithdrawSuccess() {
            this.$o2o({
                e_c: '提现',
                e_a: '成功',
                e_n: '地推合伙人平台提现页',
            }, () => {
                this.commitWithdraw();
            });
        },
        commitWithdraw() {
            const withdraw = this.value.cash / 100;
            let cash;
            switch (withdraw) {
            case 0.3:
                cash = 1;
                break;
            case 1:
                cash = 5;
                break;
            default:
                break;
            }
            openCommonWebViewByPath(`/mobile-v2/withdraw/progress?withdraw=${withdraw}${cash ? `&cash=${cash}` : ''}`);
        },
        handleWithdrawError(res) {
            if (res.data) {
                this.checkCode(res.data);
            } else if (res.msg) {
                toast(res.msg);
            } else {
                toast('网络异常，请稍后重试');
            }
        },
        configReasonDialog({ msg, btnText, handler }) {
            this.reasons = msg ? msg.split('|') : this.defaultReasons;
            this.reasonBtnText = btnText;
            this.reasonHandler = handler;
            this.openReasonDialog();
        },
        openReasonDialog() {
            this.showReasonDialog = true;
        },
        closeReasonDialog() {
            this.showReasonDialog = false;
        },
        goBack() {
            quitCurrentWebview();
        },
        goAuth() {
            if (!this.isAuth) {
                this.closeReasonDialog();
                set(cacheSelectedCashKey, JSON.stringify(this.value));
                openCommonWebViewByPath('/mobile-v2/withdraw/auth');
            }
            this.$o2o({
                e_c: '提现',
                e_a: '点击',
                e_n: '去认证',
            });
        },
        goTask() {
            if (config.versionCode >= 40600) {
                goTab('mineTab');
            } else {
                goTab('taskTab');
            }
        },
        checkCode(res) {
            switch (res.code) {
            case 14:
                this.configReasonDialog({
                    msg: res.msg,
                    btnText: '去认证',
                    handler: this.goAuth,
                });
                break;
            case 18:
                if (res.expMonStatus) {
                    this.configReasonDialog({
                        msg: `${res.msg}|可尝试兑换0.3元，秒到账！`,
                        btnText: this.defaultReasonBtnText,
                        handler: this.closeReasonDialog,
                    });
                } else {
                    this.configReasonDialog({
                        msg: res.msg,
                        btnText: '返回继续地推',
                        handler: this.goBack,
                    });
                }
                break;
            case 20:
                this.configReasonDialog({
                    msg: res.msg,
                    btnText: '去完成新手任务',
                    handler: this.goTask,
                });
                break;
            case 23:
                if (!res.expMonStatus && config.versionCode >= 40770) {
                    const title = '【想看】今天可提现1元零钱了，点击提现>>';
                    const description = '想看1元提现';
                    const hasCalendar = !!hasCalendarEventExist(title, description);
                    if (hasCalendar) {
                        this.configReasonDialog({
                            msg: res.msg,
                            btnText: this.defaultReasonBtnText,
                            handler: this.closeReasonDialog,
                        });
                    } else {
                        this.configReasonDialog({
                            msg: res.msg,
                            btnText: '加入日历提醒，不错过提现',
                            handler: () => {
                                this.handleAddCalendar(title, description);
                                this.closeReasonDialog();
                            },
                        });
                    }
                } else {
                    this.configReasonDialog({
                        msg: res.msg,
                        btnText: this.defaultReasonBtnText,
                        handler: this.closeReasonDialog,
                    });
                }
                break;
            case 24:
                this.configReasonDialog({
                    msg: res.msg,
                    btnText: '绑定并兑换',
                    handler: () => {
                        this.closeReasonDialog();
                        this.handleBindWechat(() => {
                            this.withdrawHandler(this.handleWithdrawSuccess,
                                this.handleWithdrawError);
                        });
                    },
                });
                break;
            default:
                this.configReasonDialog({
                    msg: res.msg,
                    btnText: this.defaultReasonBtnText,
                    handler: this.closeReasonDialog,
                });
                break;
            }
        },
        handleAddCalendar(title = '【想看】今天可提现1元零钱了，点击提现>>', description = '想看1元提现', mode = 'FREQ=DAILY;INTERVAL=1;COUNT=1', offsetDay = 1) {
            const target = new Date();
            target.setDate(target.getDate() + offsetDay);
            target.setHours(7);
            target.setMinutes(0);
            target.setSeconds(0);
            addCalendarEvent({
                title,
                description,
                startTime: target.getTime(),
                endTime: target.getTime() + 600000,
                location: 'http://www.xiangkannews.cn/a?dLHost=goto&dLPath=newsTab&start_source=calendar',
                mode,
            });
        },
    },
};
</script>

