import { getRegMethod } from '../api/miVerify';

/**
 * https://wiki.n.miui.com/pages/viewpage.action?pageId=73330071
 *
 * @author 文桥 <wenqiao1@xiaomi.com>
 * @export
 * @param {Functuon} handler(verifyObj)
 */
export default function miVerify(data, handler, type) {
    // 在页面加载完成后尽可能早的调用初始化方法，以搜集更多的信息，注意：对于一项验证只需初始化一次!!!
    window.initMiverify(
        {
            // 和业务方后端约定的注册验证服务的API，http method默认为get。不满足需求的可以使用regMethod。
            // registerApi: '/biz/register',
            // 如果后端对请求格式有要求或http method为其他方式的，
            // 可以传入自定义的注册函数，参数中传入返回格式化好的response的callback，
            // 参见下面的regMethod示例。有此参数时就不用再传registerApi这个参数
            regMethod: getRegMethod(data),
            onSuccess: function onSuccess(token) { // 成功回调
                console.log('Verify success!');
                console.log(token);
                // 4. 二次验证 start
                // 此时成功只是客户端与人机验证平台获取了成功的结果
                // 为避免作恶者篡改结果，还需要业务方 Server 与人机验证平台Server进行二次验证
                // if (typeof onSuccessRequest === 'function') {
                //     onSuccessRequest(token);
                // }
                window.miVerify[type].onSuccessRequest(token);
                // 二次验证 end
            },
            // 交互验证关闭的回调，code标志不同情况
            onClose: function onClose(code) {
                console.log('Verifying close!');
                // code 关闭原因：0 验证成功，1 用户手动关闭，2 eventId 过期，3 系统错误
                console.log(code);
                // if (typeof onCloseRequest === 'function') {
                //     onCloseRequest(code);
                // }
                window.miVerify[type].onCloseRequest(code);
            },
        },
        handler,
    );
}

window.miVerify = {
    taskTime: {},
    withdraw: {},
    partnerWithdraw: {},
};
