<template>
    <transition name="fade">
        <div
            v-if="show"
            class="dialog"
            @click.self="closeHandler($event, 'outside')">
            <div class="withdraw-dialog__content">
                <div
                    @click="closeHandler"
                    class="withdraw-dialog__close" />
                <div class="withdraw-dialog__inner">
                    <h1 v-if="title">{{ title }}</h1>
                    <slot>
                        <div class="withdraw-dialog__msg">
                            <slot name="msg">
                                <p
                                    v-for="msg in msgs"
                                    :key="msg"
                                    v-html="msg" />
                            </slot>
                        </div>
                        <Button
                            :customStyle="{
                                width: '100%',
                                height: '0.43rem',
                                fontSize: '0.16rem',
                                background: '#0EC217'
                            }"
                            @click="clickHandler">{{ btnText }}</Button>
                    </slot>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import Button from '@/components/Button';

export default {
    name: 'Dialog',
    components: {
        Button,
    },
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        outsideClose: {
            type: Boolean,
            default: true,
        },
        title: {
            type: String,
            default: '',
        },
        msgs: {
            type: Array,
            default: () => ([]),
        },
        btnText: {
            type: String,
            default: '',
        },
        clickHandler: {
            type: Function,
            default: () => {},
        },
    },
    methods: {
        closeHandler(e, position) {
            if (position === 'outside' && !this.outsideClose) return;

            this.$emit('update:show', false);
            this.$emit('close');
        },
    },
};
</script>

