// import axios from 'axios';
import request from './request';

export function getRegMethod(data) {
    return (cb) => {
        // instance.defaults.headers.post['Content-Type'] = 'application/json';
        request.get('/api/v4/sec/captcha/register', data)
            .then((res) => {
                if (res.data.code === 0) {
                    console.log('Register success!');
                    if (typeof cb === 'function') {
                        cb(res.data.data);
                        window.miVerify.register = true;
                    }
                } else {
                    console.log('Register callback error!');
                    window.miVerify.register = false;
                }
            })
            .catch((res) => {
                console.log('Register request error!');
                window.miVerify.register = false;
                console.log(res);
            });
    };
}

export function test() { }
